.wrapper {
    display: flex;
    align-items: center;
    gap: 12px;
  }
  
  .username {
    font-weight: 700;
  }
  
  .logout_button{
    background-color: black;
    color: white;
    border: none;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;

  }
  .logout_button:hover {
    background-color: #fd002a;
    box-shadow: 5px 5px 8px 2px #667bd646;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }