.link {
  display: inline-block;
  text-decoration: none;
  padding: 5px;
  font-weight: 700;
  color: #2a363b;
  text-transform: uppercase; /* Всі букви будуть великими */
  margin-left: 100px;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1); /* Додаємо плавний перехід */
}

.link.active {
  border-bottom: 2px solid #2a363b; /* Стиль для активного посилання */
}